@import url('https://fonts.googleapis.com/css2?family=Aubrey&family=Potta+One&family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap');
*{
    padding: 0;
    margin: 0;
}
.auth{
    width: 100%;
    height: 80vh;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}
#uname{
    width: 30%;
    height: 30px;
    font-size: 19px;
    padding: 3px;
    outline: none;
    border: 3px solid #06d191;
    border-radius: 8px;
}
#unamebtn{
    position: absolute;
    margin-left: 23%;
    background: none;
    border: none;
    cursor: pointer;
}
#send-arrow{
    color: #06d191;
}