@import url('https://fonts.googleapis.com/css2?family=Aubrey&family=Potta+One&family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap');
#aside {
    width: 100%;
    height: 88vh;
    float: left;
    display: flex;
    align-items: end;
    justify-content: end;
}

form{
    width: 100%;
    height: 60px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0px 10px;
}

.new-message-form{
    display: flex;
    align-items: center;
    justify-content: end;
}

#msg{
