    outline: none;
    border: none;
    width: 480px;
    height: 40px;
    font-family: Roboto;
    font-size: 16px;
    padding: 5px;
    border-radius: 8px;
    border: 3px solid#07e9a3;
}
.input-btns{
    margin-left: 4px;
    padding: 13px 12px;
    border-radius: 50%;
    color: #fff;
    background: #07e9a3;
    border: none;
    cursor: pointer;
}
#sumit-btn{
    padding: 12px 12px;
}
#user-info{
    background: none;
    border: none;
    cursor: pointer;
}
#user-info-img{
    width: 30px;
}
.settings{
    float: left;
    padding: 0px 10px;
    margin-right: 40px;
}
#chatstnarea{
    width: 190px;
    height: 175px;
    background: rgb(231, 231, 231);
    display: none;
    position: absolute;
    margin-top: -35px;
    margin-right: -160px;
    border-radius: 12px;
    display: flex;
    flex-direction: column;
    font-family: Roboto;
    display: none;
}
.user-data{
    padding: 10px;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    gap: 10px;
}
#user-info-img-sml{
    width: 30px;
}

#xmark{
    background: none;
    border: none;
    cursor: pointer;
}

.apperance{
    margin-top: 14px;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    gap: 35px;
}

.switch {
    --false: #000000;
    --true: #07e9a3;
   }
   
   input[type=checkbox] {
    appearance: none;
    height: 22px;
    width: 35px;
    background-color: #4e4e4e;
    position: relative;
    cursor: pointer;
    border-radius: 40%;
   }
   
   input[type=checkbox]::before {
    content: '';
    display: block;
    height: 22px;
    width: 22px;
    transform: translate(-50%, -50%);
    position: absolute;
    top: 50%;
    left: calc(7px/2 + .3em);
    background-color: var(--false);
    border-radius: 50%;
    transition: .3s ease;
   }
   
   input[type=checkbox]:checked::before {
    background-color: var(--true);
    left: calc(100% - (7px/2 + .3em));
   }

#theme{
    margin-top: 14px;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}
#theme h4{
    margin-top: 5px;
}

#theme button{
